.text-small {
    font-size: 0.9rem !important;
}

.text-uppercase {
    letter-spacing: 0.1em !important;
}

.headings-font-family {
    font-family: $headings-font-family !important;
}

.font-family-base {
    font-family: $font-family-base !important;
}

.bg-cover {
    background-size: cover !important;
}

.bg-position-center {
    background-position: center center !important;
}

.bg-gray {
    background: #f4f4f4 !important;
}

.icon {
    font-size: 1.4rem;
    color: #000;
}

.overlay {
    width: 100%;
    height: 100%;
    background: rgba($brand-primary, 0.93);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;

    & + .content {
        position: relative;
        z-index: 3;
    }
}

.no-anchor-style {
    text-decoration: none;
    color: inherit;
    &:hover {
        color: inherit;
    }
}

.lined {
    &::after {
        content: '';
        display: block;
        width: 6.25rem;
        height: 2px;
        margin: 1.25rem auto;
        background: $brand-primary;
    }

    &-compact {
        &::after {
            margin-top: 0.5rem;
        }
    }
}


.social-link {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    color: #fff;
    text-decoration: none;
    background: $brand-primary;
    font-size: 0.8rem;

    &:hover, &:focus {
        color: #fff;
        text-decoration: none;
    }

    &-facebook {
        background: #4460ae;
    }
    &-twitter {
        background: #3cf;
    }
    &-instagram {
        background: #cd4378;
    }
    &-google-plus {
        background: #c21f25;
    }
}

.link-scroll {
    &.active {
        color: $brand-primary;
    }
}

.hero-text {
    @include media-breakpoint-up(lg) {
        font-size: 7rem !important;
    }
}

.text-gray {
    color: #999 !important;
}
