/*
*
* =====================
* GENERAL
* =====================
*
*/

body {
    overflow-x: hidden;
}

section {
    position: relative;
    padding: 5rem 0;
    overflow-x: hidden;
}

.scroll-btn {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    border: 2px solid #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 0.8rem;
    position: absolute;
    bottom: 3rem;
    left: calc(50% - 1.25rem);
    z-index: 999;

    &:hover, &:focus {
        color: #fff;
        text-decoration: none;
    }
}



/*
*
* =====================
* NAVBAR
* =====================
*
*/
.navbar {
    .nav-link {
        font-family: $headings-font-family;
        font-weight: 700;
        color: #555;

        &:hover {
            color: $brand-primary;
        }
    }
}

.navbar-toggler {
    outline: none !important;

    &:hover, &:focus {
        background: $brand-primary;
        color: #fff;
    }
}



/*
*
* =====================
* HERO
* =====================
*
*/
.hero {
    min-height: 100vh;
    height: 100vh;
}


#map {
    height: 25rem;
}
